import React, { useEffect, useState } from 'react'
import boardTile from "../assets/boardTile.jpg"
import { useUpdate } from '../utils/Context'
import axios from 'axios'

const BoardTile = () => {
    const [boards, setBoards] = useState([])
    // const boards = []
    const {user} = useUpdate()
    // let userBoards = []

    useEffect(() => {
        fetchBoard()
    }, [])

    async function fetchBoard() {
        const userBoards = await axios.post(
            `${process.env.REACT_APP_API_URL}/boards/get-boards`, {
                user,
            }
        )
        setBoards(userBoards)
        console.log(boards)
    }

    return (
        <div className='flex flex-col w-44'>
            <h4 className="mt-10 text-center text-2xl font-bold leading-9 tracking-tight text-white">{boards[0]}</h4>
        </div>
    )
}

export default BoardTile